<template>
  <AppLayout class="item-page">
    <template v-slot:default>
      <FormsHeader class="item-page__forms-header" :form-id="itemId" />

      <VTabs
        :items="tabsItems"
        :current-tab="currentTab"
        @change="changeCurrentTab"
      />

      <transition name="item-page__forms" mode="out-in">
        <GeneralForm v-if="currentTab === 'general'" />
        <AuthorsForm
          @success="showSuccess = true"
          v-else-if="currentTab === 'authors'"
        />
        <ResourcesForm
          v-else-if="currentTab === 'resources'"
          @success="showSuccess = true"
        />
        <DatesForm v-else-if="currentTab === 'keyDates'" />
        <DisclosureForm v-else-if="currentTab === 'disclosure'" />
        <OrganizationsForm
          v-else-if="currentTab === 'organizations'"
          @success="showSuccess = true"
        />
        <TechnologyForm v-else-if="currentTab === 'technology' && isTechType" />

        <SourceCodeForm
          v-else-if="currentTab === 'source-code' && !isTechType"
        />

        <DescriptionForm
          v-else-if="currentTab === 'description' && !isTechType"
        />
      </transition>

      <AddedModal :show="showSuccess" />

      <GeneralPositionModal
        @submit="onSubmit"
        :loading="sendingForms"
        :show="showGeneralPosition"
        @close="showGeneralPosition = false"
      />

      <CompleteModal :show="showComplete" />
    </template>

    <template v-slot:footer v-if="hasEditForms">
      <ItemPageFooter
        :complete="completed"
        @send="showGeneralPosition = true"
      />
    </template>
  </AppLayout>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

import VTabs from "@/components/tabs/Tabs";
import AppLayout from "@/components/app/AppLayout";
import DatesForm from "@/components/forms/DatesForm";
import AddedModal from "@/components/modals/AddedModal";
import GeneralForm from "@/components/forms/GeneralForm";
import FormsHeader from "@/components/forms/FormsHeader";
import AuthorsForm from "@/components/authors/AuthorsForm";
import CompleteModal from "@/components/modals/CompleteModal";
import ResourcesForm from "@/components/resources/ResourcesForm";
import ItemPageFooter from "@/components/item-page/ItemPageFooter";
import DisclosureForm from "@/components/disclosure/DisclosureForm";
import TechnologyForm from "@/components/technology/TechnologyForm";
import GeneralPositionModal from "@/components/modals/GeneralPositionModal";
import OrganizationsForm from "@/components/organizations/OrganizationsForm";
import {
  FORMS_TABS_ITEMS,
  FormsTabKeys,
  FormsTabStatus,
  FormStatus,
  FormTypes,
} from "@/constants/FormConstants";
import DescriptionForm from "@/components/technology/DescriptionForm";
import SourceCodeForm from "@/components/technology/SourceCodeForm";

export default {
  name: "ItemView",
  components: {
    SourceCodeForm,
    DescriptionForm,
    VTabs,
    DatesForm,
    AppLayout,
    AddedModal,
    AuthorsForm,
    FormsHeader,
    GeneralForm,
    ResourcesForm,
    CompleteModal,
    DisclosureForm,
    TechnologyForm,
    ItemPageFooter,
    OrganizationsForm,
    GeneralPositionModal,
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("application", ["getPass"]),
    ...mapGetters("formInfo", ["getFormType", "hasEditForms"]),
    ...mapGetters("general", { generalFormStatus: "getFormStatus" }),
    ...mapGetters("authors", { authorsFormStatus: "getFormStatus" }),
    ...mapGetters("dates", { datesFormStatus: "getFormStatus" }),
    ...mapGetters("technology", { technologyFormStatus: "getFormStatus" }),
    ...mapGetters("description", { descriptionFormStatus: "getFormStatus" }),
    ...mapGetters("organizations", {
      organizationsFormStatus: "getFormStatus",
    }),
    ...mapGetters("disclosure", {
      disclosureFormStatus: "getFormStatus",
    }),
    ...mapGetters("resources", {
      resourcesFormStatus: "getFormStatus",
    }),
    itemId() {
      const id = Number(this.$route.params.id);

      if (!Number.isNaN(id) && id !== 0) {
        return id;
      }

      return FormStatus.Draft;
    },
    currentTab() {
      return this.$route.query.tab || "general";
    },
    completed() {
      const successGeneralForm =
        this.generalFormStatus === FormsTabStatus.Filled;
      const successAuthorsForm =
        this.authorsFormStatus === FormsTabStatus.Filled;
      const successDatesForm = this.datesFormStatus === FormsTabStatus.Filled;
      const successTechnologyForm =
        this.technologyFormStatus === FormsTabStatus.Filled;
      const successOrganizationsForm =
        this.organizationsFormStatus === FormsTabStatus.Filled;
      const successDisclosureForm =
        this.disclosureFormStatus === FormsTabStatus.Filled;
      const successResourcesForm =
        this.resourcesFormStatus === FormsTabStatus.Filled;
      const successDescriptionForm = !this.isTechType
        ? this.descriptionFormStatus === FormsTabStatus.Filled
        : true;

      return (
        successDatesForm &&
        successGeneralForm &&
        successAuthorsForm &&
        successResourcesForm &&
        successDisclosureForm &&
        successTechnologyForm &&
        successOrganizationsForm &&
        successDescriptionForm
      );
    },
    tabStatuses() {
      return {
        [FormsTabKeys.KeyDates]: this.datesFormStatus,
        [FormsTabKeys.General]: this.generalFormStatus,
        [FormsTabKeys.Authors]: this.authorsFormStatus,
        [FormsTabKeys.Resources]: this.resourcesFormStatus,
        [FormsTabKeys.Technology]: this.technologyFormStatus,
        [FormsTabKeys.Disclosure]: this.disclosureFormStatus,
        [FormsTabKeys.SourceCode]: this.technologyFormStatus,
        [FormsTabKeys.Description]: this.descriptionFormStatus,
        [FormsTabKeys.Organizations]: this.organizationsFormStatus,
      };
    },
    isTechType() {
      return this.getFormType === FormTypes.Tech;
    },
    tabsItems() {
      let list = FORMS_TABS_ITEMS.map((x) => ({
        ...x,
        title: this.localize[x.title].text,
      })).slice();

      if (this.isTechType) {
        list.splice(FORMS_TABS_ITEMS.length - 2, 2);
      } else {
        list.splice(FORMS_TABS_ITEMS.length - 3, 1);
      }

      return list.map((x) => ({
        ...x,
        status: this.tabStatuses[x.key] || FormsTabStatus.NotFilled,
      }));
    },
  },
  data() {
    return {
      sendingForms: false,
      showSuccess: false,
      showComplete: false,
      showGeneralPosition: false,
    };
  },
  watch: {
    showSuccess(nextState, prevState) {
      if (nextState !== prevState) {
        const timout = setTimeout(() => {
          this.showSuccess = false;

          clearTimeout(timout);
        }, 2000);
      }
    },
    isTechType(nextState, prevState) {
      if (
        nextState !== prevState &&
        (this.currentTab === FormsTabKeys.Description ||
          this.currentTab === FormsTabKeys.SourceCode)
      ) {
        this.changeCurrentTab(FormsTabKeys.Technology);
      } else if (
        nextState !== prevState &&
        this.currentTab === FormsTabKeys.Technology
      ) {
        this.changeCurrentTab(FormsTabKeys.SourceCode);
      }
    },
    showComplete(nextState, prevState) {
      if (nextState && !prevState) {
        const timout = setTimeout(() => {
          this.showComplete = false;

          clearTimeout(timout);
        }, 2000);
      }
    },
  },
  methods: {
    ...mapActions("authors", ["getAuthors"]),
    ...mapActions("formInfo", ["sendForms"]),
    changeCurrentTab(tab) {
      this.$router.replace({ ...this.$route, query: { tab } });
    },
    onSubmit() {
      this.sendingForms = true;

      this.sendForms().finally(() => {
        this.showGeneralPosition = false;
        this.showComplete = true;
        this.sendingForms = false;
      });
    },
  },
  mounted() {
    if (!this.getPass) {
      this.$router.replace("/");
    }

    this.getAuthors();
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/mixins.scss";

.item-page__forms-header {
  margin: 19px 0 29px;
}

.item-page__forms-enter-active,
.item-page__forms-leave-active {
  @include transition(opacity);
}

.item-page__forms-enter-from,
.item-page__forms-leave-to {
  opacity: 0;
}
</style>
